import "./Paper.scss";
import dload from "../../../assets/iconos/iconoDownload.svg";
import pgPaper from "../../../assets/papers/DATOS EN FUGA _ Panorama General.pdf";
import rvPaper from "../../../assets/papers/DATOS EN FUGA_ Reportantes de Vulnerabilidades.pdf";

const Paper = () => {
  return (
    <div className="paper" id="paper">
      <h2 className="paper__title">PAPER</h2>
      <div className="paper__content">
        <p className="paper__text">
          <strong>
            Panorama general sobre la seguridad de la informacin, las
            vulnerabilidades y los incidentes en Argentina
          </strong>
          <br />
          La palabra vulnerabilidad se usa en muchos contextos, qu significa
          cuando hablamos de tecnologas de la informacin? Qu pasa si no
          atendemos las debilidades de los sistemas en los que operamos todos
          los das? Qu hace el Estado argentino para garantizar sistemas
          seguros y qu hacen otros pases? Ac, un panorama actualizado.
        </p>
        <button className="paper__button">
          <img src={dload} alt="download" />
          <a
            onclick="ga('send','event','Download','PDF', this.href]);"
            href={pgPaper}
            target="_blank"
            rel="noreferrer"
            download
          >
            <span>DESCARGA EL PDF</span>
          </a>
        </button>
        <p className="paper__text">
          <strong>
            Reportantes de vulnerabilidades en sistemas digitales ante la ley
            penal argentina
          </strong>
          <br />
          En un mundo rodeado de cdigo digital, cmo responde el Estado
          argentino ante una persona que informa sobre una debilidad en los
          sistemas informticos? Atiende esa alarma? Le agradece? No, le cae
          con todo el peso del sistema penal. Esto puede cambiar, conoc cmo.
        </p>
        <button className="paper__button">
          <img src={dload} alt="download" />
          <a
            onclick="ga('send','event','Download','PDF', this.href]);"
            href={rvPaper}
            target="_blank"
            rel="noreferrer"
            download
          >
            <span>DESCARGA EL PDF</span>
          </a>
        </button>
        <p className="paper__pdfCopys">*Existen copias fsicas disponibles (gratuitas) que pueden ser retiradas por las oficinas de Democracia en Red.</p>
      </div>
    </div>
  );
};

export default Paper;
